.el-button {
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    background: #FFFFFF;
    border: 1px solid #DCDFE6;
    border-color: #DCDFE6;
    color: #606266;
    -webkit-appearance: none;
    text-align: center;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    outline: none;
    margin: 0;
    -webkit-transition: .1s;
    transition: .1s;
    font-weight: 500;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    padding: 12px 20px;
    font-size: 14px;
    border-radius: 4px;
}

.el-button+.el-button {
    margin-left: 10px;
}

.el-button.is-round {
    padding: 12px 20px;
}

.el-button:hover,
.el-button:focus {
    color: #2f39ff;
    border-color: #c1c4ff;
    background-color: #eaebff;
}

.el-button:active {
    color: #2a33e6;
    border-color: #2a33e6;
    outline: none;
}

.el-button::-moz-focus-inner {
    border: 0;
}

