    background-color: #e6a23c;
}

.el-tag.el-tag--danger {
    background-color: #fef0f0;
    border-color: #fde2e2;
    color: #f56c6c;
}

.el-tag.el-tag--danger.is-hit {
    border-color: #F56C6C;
}

.el-tag.el-tag--danger .el-tag__close {
    color: #f56c6c;
}

.el-tag.el-tag--danger .el-tag__close:hover {
