    top: 20px;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    background-color: #edf2fc;
    -webkit-transition: opacity 0.3s, top 0.4s, -webkit-transform .4s;
    transition: opacity 0.3s, top 0.4s, -webkit-transform .4s;
