    background-color: #eaebff;
    border-color: #d5d7ff;
    color: #2f39ff;
    display: inline-block;
    height: 32px;
    padding: 0 10px;
    line-height: 30px;
    font-size: 12px;
    color: #2f39ff;
    border-width: 1px;
